@mixin underline-transition($op: 0.5, $delay: 0s, $bgc: $c-white) {
  &::before {
    background-color: $bgc;
    bottom: -3px;
    display: block;
    height: 1px;
    left: 0;
    // opacity: #{$op + 0.4};
    opacity: $op;
    // content: "";
    position: absolute;
    // transform: scale3d(0.9, 1, 1);
    // transform-origin: center top;
    transition: opacity $transition-timing ease-in $delay;
    width: 100%;
    // , transform $transition-timing cubic-bezier(0.18, -0.25, 0.27, 1.95) $delay;

    .data-fetched & {
      // opacity: $op;
      // transform: scale3d(1, 1, 1);
    }
  }

  &:hover {
    &::before {
      opacity: 1;
      transition: opacity 0.25s ease;
    }
  }
}

@mixin simple-underline {
  border-bottom: 1px rgba($c-white, 0.25) solid;

  &:hover {
    border-bottom: 1px rgba($c-white, 0.5) solid;
  }

  &.active {
    border-bottom: 1px rgba($c-white, 0.75) solid;
  }
}

@mixin pop-up {
  @include transition(opacity, 0.1s, ease);
  background-color: $c-cod-grey--lighten;
  border: 1px $c-white solid;
  border-radius: 3px;
  border-bottom-left-radius: none;
  bottom: 100%;
  left: calc(50% - 0.66rem);
  opacity: 0;
  padding-bottom: 0.25em;
  padding-left: 0.5em;
  padding-right: 0.5em;
  padding-top: 0.25em;
  pointer-events: none;
  position: absolute;
  z-index: 1;

  &::after,
  &::before {
    border-style: solid;
    content: '';
    display: block;
    height: 0;
    left: 0.3rem;
    position: absolute;
    width: 0;
  }

  &::before {
    border-color: $c-white transparent transparent transparent;
    border-width: 4px;
    top: calc(100% + 1px);
  }

  &::after {
    border-width: 3px;
    display: none;
    left: calc(0.3rem + 1px);
    top: 100%;
  }
}
