.section-table {
  border-radius: $section-border-radius;
  overflow: hidden;

  a {
    @include simple-underline;
    position: relative;
    text-decoration: none;
  }

  [data-constructor] {
    position: relative;

    &::before {
      content: '';
      height: 0.75rem;
      left: -0.6rem;
      opacity: 0.75;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      transition: opacity $transition-timing ease-in;
      width: 0.15rem;
    }

    &:hover::before {
      opacity: 1;
    }
  }

  .section-table__constructor {
    @include bp-xs {
      min-width: 5rem;
    }

    @include bp-sm {
      min-width: 7rem;
      padding-left: 1.6rem;
    }

    span {
      display: block;
      margin-left: 0.6rem;
    }
  }

  // Fallback F1 team colours - Taken from https://en.wikipedia.org/wiki/List_of_international_auto_racing_colours
  [data-constructor*='Lotus'] {
    &::before {
      background-color: $c-lotus;
    }
  }

  [data-constructor*='Theodore'] {
    &::before {
      background-color: $c-theodore;
    }
  }

