html {
  @include font-size(16px);
  font-family: $f-titillium-normal;
  height: 100%;
  overflow-y: scroll; // The fix below is buggy and scrollbar will always be needed anyway
  // margin-left: calc(100vw - 100%); // Jumping scrollbar fix
  // margin-right: 0; // Jumping scrollbar fix

  @include bp-xxs {
    // @include font-size(15px);
  }

  @include bp-xs {
    @include font-size(17px);
  }

  @include bp-sm {
    @include font-size(18px);
  }

  @include bp-height-sm {
    @include font-size(20px);
  }

  @include bp-height-md {
    @include font-size(22px);
  }

  @include bp-height-lg {
    @include font-size(22px);
  }

  @include bp-height-xl {
    @include font-size(24px);
  }

  @include bp-height-hd {
    @include font-size(26px);
  }
}

body {
  @include patterned-bg;
  @include font-size(14px);
  color: $c-white;
  height: 100%;
  min-width: 320px;
  position: relative;
}

.app {
  min-height: 100vh;
  // overflow: hidden;

  &.overflow-active {
    overflow: hidden;
  }

  &.race-select-active {
    height: 100vh;
    overflow-y: scroll;
  }
}

strong {
  font-family: $f-titillium-normal;
}

button {
  @include remove-highlight;
  font-family: $f-titillium-semi-bold;

  svg {
    @include remove-highlight;
  }
}

.heading {
  @include font-size(16px, 22px);
  color: $c-white;
  display: inline-block;
  font-family: $f-titillium-semi-bold;
  letter-spacing: 1px;
  position: relative;
}

.heading--large {
  @include font-size(22px, 22px);
}

.heading--medium {
  @include font-size(18px, 18px);
}

.heading--small {
  @include font-size(18px, 18px);
}

.mini-sub-heading {
  @include font-size(12px, 12px);
  position: absolute;
  bottom: 0.35rem;
  left: 0;
}

.section-table .right-align,
.right-align {
  text-align: right;
}

.section-table .center-align,
.center-align {
  text-align: center;
}

button {
  background-color: transparent;
  border: 0;
  outline: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
}

.intro__spinner {
  left: 50%;
  position: fixed;
  top: 50%;
  transform: translate(-50%, -50%);
}

.router-link-exact-active {
  pointer-events: none;
  text-decoration: none;
}

.help-pop-up {
  @include pop-up;
}

:root .leaflet-touch .leaflet-control-layers,
:root .leaflet-touch .leaflet-bar {
  border: 0;
}

:root .leaflet-bar {
  box-shadow: none;
}

:root .leaflet-pane {
  background-color: transparent;
  // opacity: 0;
  // visibility: hidden;
}

:root .leaflet-bar a:last-child {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
