.section {
  @include container;
  margin-bottom: 3rem;
  position: relative;
}

.section__content {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  position: relative;
  width: 100%;
}

.section__table-wrapper {
  border-radius: $section-border-radius;
  box-shadow: 0 2px 2px 0 rgba($c-mine-shaft, 0.14),
    0 3px 1px -2px rgba($c-mine-shaft, 0.2),
    0 1px 5px 0 rgba($c-mine-shaft, 0.12);
  overflow: hidden;
  position: relative;
  transition: height $transition-timing ease-in-out;
  width: 100%;
}

@each $dynamic-color-class, $dynamic-color in (color1, $color1),
  (color2, $color2), (color3, $color3), (color4, $color4), (color5, $color5)
{
  .section--#{$dynamic-color-class} {
    .race-information__race-info-inner {
      > p {
        color: lighten($dynamic-color, 15%);
      }
    }

    .section-table {
      background-color: rgba($dynamic-color, 0.1);
    }

    .section-content__icon {
      @include remove-highlight;
    }

    .section-content__icon {
      &:focus {
        .section-content__icon-text {
          color: lighten($dynamic-color, 40%);
        }

        svg {
          fill: lighten($dynamic-color, 35%);
        }
      }

      &:focus,
      &:active,
      &:hover {
        .section-content__icon-text {
          color: lighten($dynamic-color, 30%);
        }

        svg {
          fill: lighten($dynamic-color, 25%);
        }
      }

      &.active {
        .section-content__icon-text {
          color: lighten($dynamic-color, 10%);
        }

        svg {
          fill: lighten($dynamic-color, 10%);
        }
      }
    }

    .section-table th,
    .section-table th button {
      color: lighten($dynamic-color, 30%);
    }

    .section-table__sortable::before,
    .section-table__sortable::after {
      border-color: lighten($dynamic-color, 30%)
        transparent
        transparent
        transparent;
    }

    .mini-sub-heading {
      color: lighten($dynamic-color, 30%);
    }
  }
}

.section-content__header {
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  position: relative;
  width: 100%;
}

.section-content__heading {
  color: $c-white;
  letter-spacing: 1px;
  margin-bottom: 0.1rem;
}

.section-content__icons {
  align-items: center;
  align-self: flex-end;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.section-content__icon {
  @include font-size(13px);
  align-items: center;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font-family: $f-titillium-normal;
  padding-left: 0.6rem;
  padding-right: 0.6rem;
  position: relative;
  text-align: center;

  svg {
    @include transition(fill, 0.15s, $ease-in-out-quad);
    fill: rgba($c-white, 1);
    height: auto;
    width: 1.4rem;
  }

  &.active {
    svg {
      transform: translate3d(0, -0.1rem, 0);
    }
  }
}

.section-content__icon-text {
  @include transition(color, 0.15s, $ease-in-out-quad);
  @include font-size(14px);
  color: rgba($c-white, 0.666);
  display: inline-block;
  font-family: $f-titillium-normal;
  position: relative;
  transform: translateZ(0.1px);
}
