.section-table {
  // backface-visibility: hidden;
  // opacity: 0;
  // pointer-events: none;
  position: relative;
  // transform: translate3d(0.5rem, 0, 0);
  // transition: transform $transition-timing ease;
  width: 100%;
  // will-change: transform;

  // &.active {
  //   opacity: 1;
  //   pointer-events: initial;
  //   transform: translate3d(0, 0, 0);
  // }

  th,
  td {
    @include font-size(13px, 20px);
    display: table-cell;
    padding-bottom: 0.8em;
    padding-left: 0.3em;
    padding-right: 0.3em;
    padding-top: 0.7em;
    position: relative;
    text-align: left;
    vertical-align: middle;

    @include bp-xxs {
      padding-left: 0.45em;
      padding-right: 0.45em;
    }

    @include bp-xs {
      padding-left: 0.5em;
      padding-right: 0.5em;
    }

    @include bp-sm {
      padding-left: 0.6em;
      padding-right: 0.6em;
    }

    button {
      font-size: inherit;
    }

    &.hide-xs {
      display: none;

      @include bp-xs {
        display: table-cell;
      }
    }

    &.hide-xxs {
      display: none;

      @include bp-xs {
        display: table-cell;
      }
    }

    &.hide-sm {
      display: none;

      @include bp-sm {
        display: table-cell;
      }
    }

    &:first-of-type {
      padding-left: 0.5rem;

      @include bp-xxs {
        padding-left: 0.6rem;
      }

      @include bp-xxs {
        padding-left: 0.8rem;
      }
    }

    &:last-of-type {
      padding-right: 0.6rem;

      @include bp-xxs {
        padding-right: 0.7rem;
      }

      @include bp-xxs {
        padding-right: 0.8rem;
      }
    }
  }

  th {
    font-family: $f-titillium-semi-bold;
  }

  tr {
    border-bottom: 1px rgba($c-mine-shaft, 0.25) solid;
    width: 100%;

    &:nth-of-type(odd) {
      background-color: rgba($c-cod-grey, 0.25);
    }

    &:first-of-type {
      background-color: rgba($c-cod-grey, 0.5);
      border-top-left-radius: $section-border-radius;
      border-top-right-radius: $section-border-radius;
      overflow: hidden;
    }

    &:last-of-type {
      border-bottom: 0;
      border-bottom-left-radius: $section-border-radius;
      border-bottom-right-radius: $section-border-radius;
      overflow: hidden;
    }
  }

  a {
    color: $c-white;
  }

  .section-table__flag {
    @include bp-lt-xxs {
      padding-right: 0;
      padding-left: 0;
      padding-top: 0;
      padding-bottom: 0;
    }

    img {
      display: none;

      @include bp-xxs {
        display: block;
      }
    }

    @include bp-xxs {
      display: table-cell;
      min-width: 1.5rem;
      width: 1.5rem;
    }

    @include bp-xs {
      width: 2rem;
      min-width: 2rem;
    }

    img {
      max-height: 0.8rem;
    }

    .section-table__nationality {
      @include pop-up;
    }

    &:hover {
      .section-table__nationality {
        opacity: 1;
      }
    }
  }

  .top-align {
    vertical-align: top;
  }
}

.section-table__table {
  position: relative;
  width: 100%;
}

.section-table__no {
  color: rgba($c-white, 0.66);
}

.section-table__grid {
  span {
    display: none;
    @include bp-xs {
      display: inline-block;
    }
  }
}

.section-table__given-name {
  display: none;

  @include bp-xxs {
    display: inline-block;
  }
}

.section-table__sortable {
  color: $color3;
  cursor: pointer;
  text-decoration: underline;
  white-space: nowrap;

  &::before,
  &::after {
    @include transition(transform, 0.1s, ease);
    border-style: solid;
    border-width: 0.3rem 0.3rem 0;
    content: '';
    display: inline-block;
    height: 0;
    margin-right: 0.3rem;
    opacity: 0;
    top: 50%;
    transform: scale3d(1, 0, 1);
    transform-origin: 50% 0.1rem;
    vertical-align: middle;
    width: 0;
  }

  &::after {
    display: none;
  }

  &.active--desc {
    &::before,
    &::after {
      opacity: 1;
      transform: scale3d(1, -1, 1);
    }
  }

  &.active--asc {
    .section-table--qualifying & {
      text-decoration: none;
    }

    &::before,
    &::after {
      opacity: 1;
      transform: scale3d(1, 1, 1);
    }
  }
}

.section-table__sortable.section-table__position {
  &::before {
    display: none;
  }

  &::after {
    display: inline-block;
    margin-left: 0.3rem;
    margin-right: 0;
  }
}

.section-table__sortable.section-table__sortable--age.active--desc::after {
  transform: scale3d(1, 1, 1);
}

.section-table__sortable.section-table__sortable--age.active--asc::after {
  transform: scale3d(1, -1, 1);
}

.section-table__grid-diff--pos {
  color: $c-fruit-salad;
}

.section-table__grid-diff--neg {
  color: $c-pomegranate;
}

.section-table__quali-time {
  display: inline-block;
}

.section-table__quali-compare {
  display: inline-block;
}

.section-table__quali-prev {
  color: rgba($c-white, 0.5);
  display: inline-block;
  width: 100%;
}

.section-table__quali-neg {
  color: $c-fruit-salad;
  display: inline-block;
  width: 100%;
}

.section-table__quali-pos {
  color: $c-pomegranate;
  display: inline-block;
  width: 100%;
}

.section-table__quali-prev--invalid {
  + .section-table__quali-pos {
    color: lighten($c-pomegranate, 15%);
    filter: grayscale(0.75);
  }
}

.section-table__quali-prev--invalid {
  + .section-table__quali-neg {
    color: lighten($c-fruit-salad, 15%);
    filter: grayscale(0.75);
  }
}
