.race-select {
  @include patterned-bg;
  display: flex;
  flex-direction: column;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  transition: opacity $transition-timing ease-in;
  width: 100%;
  z-index: $z-race-select;

  &.active {
    opacity: 1;
    pointer-events: all;
  }
}

.race-select__inner {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  overflow: auto;
  width: 100%;
}

.race-select__overlay {
  @include container;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  left: 50%;
  opacity: 0;
  padding-bottom: 4rem;
  padding-top: 8.5rem;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
  transition: opacity $transition-timing ease-in-out;
  width: 100%;

  &.active {
    opacity: 1;
    pointer-events: all;
  }

  button,
  span {
    @include font-size(15px, 20px);
    @include remove-highlight;
    align-items: center;
    border: 1px rgba($c-white, 0.5) solid;
    border-radius: 4px;
    color: $c-white;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 0.5em;
    padding-left: 0.5em;
    padding-right: 0.5em;
    padding-top: 0.5em;
    position: relative;
    text-align: center;
    word-break: break-word;

    @include bp-xs {
      padding-bottom: 0.75rem;
      padding-left: 0.75rem;
      padding-right: 0.75rem;
      padding-top: 0.75rem;
    }

    &:hover {
      background-color: rgba($c-white, 0.05);
    }

    &:active,
    &:focus {
      background-color: rgba($c-white, 0.1);
    }
  }

  span {
    border: 1px rgba($c-white, 0.1) solid;
    color: rgba($c-white, 0.666);
    cursor: default;

    &:hover,
    &:active,
    &:focus {
      background-color: transparent;
    }

    p {
      @include font-size(13px, 17px);
      color: lighten($color1, 15%);
      margin-bottom: 0;
      margin-top: 0.5em;
    }
  }
}

.race-select__overlay--season {
  grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
}
