.show-seasons {
  @include transition;
  @include full-flex;
  align-items: center;
  height: $show-seasons-height;
  opacity: 0;

  &.active {
    opacity: 1;
  }
}

.show-seasons__btn {
  @include transition(background-color, 0.15s, $ease-in-out-expo);
  align-items: center;
  background-color: rgba($c-white, 0.075);
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  font-family: $f-titillium-normal;
  justify-content: center;
  padding-bottom: 1em;
  padding-left: 1.5em;
  padding-right: 1.25em;
  padding-top: 1em;
  position: relative;

  &:active,
  &:hover {
    background-color: rgba($c-white, 0.085);
  }

  &:focus,
  &:hover {
    .show-seasons__svg {
      transform: translate3d(0, 1px, 0);
    }
  }
}

.show-seasons__btn-text {
  @include font-size(14px, 14px);
  color: $c-white;
  margin-right: 0.75rem;
}

.show-seasons__svg {
  @include transition(transform opacity, 0.15s, $ease-in-expo);
  display: block;
  fill: $c-white;
  opacity: 0;
  width: 0.5rem;

  &.active {
    opacity: 1;
  }
}
