.race-header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-bottom: 3rem;
  margin-top: 2rem;
  position: relative;
  width: 100%;
}

.race-header__top {
  @include container;
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  z-index: $z-race-header__top;
}

.race-header__logo-link {
  @include remove-highlight;
  display: block;
  height: 2rem;

  @include bp-xxs {
    height: 2.55rem;
  }
}

.race-header__logo {
  @include remove-highlight;
  display: block;
  height: 100%;
}

.race-header__info {
  display: flex;
  flex-direction: column;
  position: relative;
}

.race-header__race {
  @include font-size(17px);
  @include simple-underline;
  color: rgba($c-white, 0.75);
  cursor: pointer;
  display: inline;
  margin-left: 1rem;
  margin-top: -0.2rem;
  position: relative;
  text-align: right;

  @include bp-xxs {
    @include font-size(20px);
  }

  @include bp-xs {
    @include font-size(22px);
  }
}

.race-header__circuit {
  @include font-size(14px);
  color: rgba($c-white, 0.5);
  margin-top: 0.2rem;
  position: relative;
  text-align: right;
  // color: lighten($color1, 35%);

  @include bp-xxs {
    @include font-size(15px);
  }
}

.race-header__round-and-date {
  margin-left: auto;
  margin-top: 0.35em;
  padding-left: 1rem;
  text-align: right;
  width: 100%;
}

.race-header__round,
.race-header__year-btn,
.race-header__date {
  @include font-size(15px);
  color: rgba($c-white, 0.75);
  vertical-align: baseline;

  @include bp-xxs {
    @include font-size(16px);
  }
}

.race-header__race-btn {
  display: inline;
  text-align: right;

  &.active {
    .race-header__race {
      border-bottom: 1px $c-white solid;
      color: $c-white;
    }
  }
}

.race-header__year-btn {
  @include simple-underline;
  cursor: pointer;
  font-family: $f-titillium-semi-bold;

  &.active {
    color: $c-white;
  }
}

.race-header__race-btn--offline,
.race-header__race-btn--offline .race-header__race,
.race-header__year-btn--offline {
  border-bottom: 0;
  cursor: default;
  font-family: $f-titillium-normal;

  &:hover {
    border-bottom: 0;
  }
}

.race-header__race-btn--offline {
  .race-header__race {
    font-family: $f-titillium-semi-bold;
  }
}
