// Clearfix
@mixin clearfix {
  &::before,
  &::after {
    content: '';
    display: table;
  }

  &::after {
    clear: both;
  }
}

// Visually hide an element
@mixin visually-hidden {
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

// Full width
@mixin full-width {
  float: left;
  position: relative;
  width: 100%;
}

// Container
@mixin full-width-container {
  @include clearfix;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
  width: 100%;
}

// Remove tap highlight on touch devices
@mixin remove-highlight {
  outline: none;
  -webkit-tap-highlight-color: transparent !important;
}

@mixin hover-brightness {
  transition: filter $transition-timing ease;

  &:hover,
  &:active {
    filter: brightness(1.05);
  }
}

// @include full-flex
@mixin full-flex {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}

// @include container
@mixin container {
  margin-left: auto;
  margin-right: auto;
  max-width: $container-max-width;
  padding-left: 1rem;
  padding-right: 1rem;

  @include bp-xs {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  @include bp-sm {
    // max-width: 768px;
  }
}

// @include transition
// @include transition(transform opacity, 500ms, ease, 0s);
@mixin transition($properties: opacity, $duration: 200ms, $method: ease) {
  $value: ();
  @each $property in $properties {
    @if length($value) > 0 {
      $value: append($value, unquote(',') $property $duration $method);
    } @else {
      $value: append($value, $property $duration $method);
    }
  }
  transition: $value;
}

@mixin transiaation(
  $properties: all,
  $duration: 200ms,
  $method: ease,
  $delay: 0ms,
  $willChange: false,
  $backFaceVisibility: false
) {
  $value: ();
  $valueWillChange: ();
  @each $property in $properties {
    @if length($value) > 0 {
      $value: append($value, unquote(",") #{$property} $duration $method $delay);
      $valueWillChange: append($valueWillChange, unquote(",") #{$property});
    } @else {
      $value: append($value, #{$property} $duration $method $delay);
      $valueWillChange: append($valueWillChange, #{$property});
    }
  }
  transition: $value;
  @if $willChange {
    will-change: $valueWillChange;
  }
  @if $backFaceVisibility {
    backface-visibility: hidden;
  }
}

@mixin patterned-bg {
  background-color: $c-mine-shaft;
  background-image: url('/img/bg.png');
  background-repeat: repeat;
}
