.site-footer {
  @include full-flex();
  align-items: flex-end;
  align-self: flex-end;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.site-footer__cite {
  text-align: left;
}

.site-footer__info {
  text-align: right;
}

.site-footer__cite,
.site-footer__info {
  color: rgba($c-white, 0.5);
  font-size: 13px;
  line-height: 16px;
  // width: calc(50% - 1rem);

  a {
    color: rgba($c-white, 0.5);
    text-decoration: underline;
  }
}
